.App {
  margin-top: 60px;
}
.loading-sec {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #00e7ff05;
}
.heading-section {
  margin-bottom: 70px;
}
.heading-section h1 {
  position: relative;
  width: max-content;
  margin: auto;
}
.heading-section h1:before {
  content: "";
  position: absolute;
  width: 41%;
  background-color: #4dc6e4;
  bottom: -15px;
  left: 0;
  right: 0;
  height: 4px;
  margin: auto;
  border-radius: 10px;
}
.button-list li:not(:last-of-type) {
  margin-bottom: 20px;
}
.button-list button {
  border: none;
  background: none;
  position: relative;
  padding-left: 40px;
  font-size: 18px;
  transition: 0.4s;
}
.button-list button.active {
  color: #4dc6e4;
  font-size: 28px;
}
.button-list button.active::before {
  content: "";
  position: absolute;
  height: 100%;
  left: 0;
  background-color: #4dc6e4;
  width: 4px;
  border-radius: 10px;
  transition: 0.4s;
}
h5.company {
  background: #4dc6e42e;
  padding: 10px 15px;
  border-radius: 10px;
  width: max-content;
  font-size: 16px;
  font-weight: 600;
  margin: 20px 0;
  color: #19404a;
}
h5.dates {
  color: #597d86;
  font-size: 16px;
}
.text-contents ul {
  margin-top: 50px;
}
.text-contents li {
  padding-left: 50px;
  margin-bottom: 30px;
  position: relative;
  color: #19404a;
}
.text-contents li svg {
  font-size: 32px;
  position: absolute;
  left: 0;
  color: #4dc6e4;
}
